import React from 'react';
import { ChakraProvider } from '@chakra-ui/react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { Container } from '@chakra-ui/react';

import { Splash } from './features/splash/Splash';
import { Home } from './features/home/Home';
import { LogIn } from './features/log-in/LogIn';
import { SignUp } from './features/sign-up/SignUp';
import { Subsidiary } from './features/sign-up/subsidiary/Subsidiary';
import { UserHome } from './features/user/home/UserHome';

import './App.css';

function App() {
  return (
    <ChakraProvider>
      <Container>
        <BrowserRouter basename={"/unibuild"}>
          <Routes>
            <Route path="/" element={<Splash />} />
            <Route path="/home" element={<Home />} />
            <Route path="/log-in" element={<LogIn />} />
            <Route path="/sign-up" element={<SignUp />} />
            <Route path="/sign-up/subsidiary" element={<Subsidiary />} />

            <Route path="/user/home" element={<UserHome />} />
          </Routes>
        </BrowserRouter>
      </Container>
    </ChakraProvider>
    );
}

export default App;
