import React from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { Center, VStack, Button, Heading } from '@chakra-ui/react';
import { setSubsidiary } from '../signUpSlice';
import { Logo } from '../../../common/logo/Logo';

const subsidiaries = ["UB Holding", "UB Construction", "UB Consultancy", "UB General Trading", "UB Management & Trading", "UB Med", "UB Technology", "UB Training", "Unleashed Sports"];

export function Subsidiary() {

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleClick = function (subsidiary) {
    dispatch(setSubsidiary(subsidiary));
    navigate('/sign-up');
  }

  return (
    <VStack minH="80vh">
      <Center h="20vh"><Logo /></Center>
      <VStack spacing={4}>
        <Heading size="xs">Choose a Subsidiary</Heading>
        {subsidiaries.map(x => <Button colorScheme="blue" key={x} w={250} onClick={() => handleClick(x)}>{x}</Button>)}
      </VStack>
    </VStack>
    );
}
