import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    subsidiary: null,
};

export const slice = createSlice({
  name: 'signUp',
  initialState,
  reducers: {
    setSubsidiary: (state, action) => {
      state.subsidiary = action.payload;
    }
  }
});

export const { setSubsidiary } = slice.actions;

export const selectSubsidiary = (state) => state.signUp.subsidiary;

export default slice.reducer;
