import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { Center, VStack, Heading, Input, InputGroup, InputLeftElement, InputRightAddon, Icon, Button } from '@chakra-ui/react';
import { FiUser, FiLock, FiMail, FiSmartphone } from 'react-icons/fi';
import { selectSubsidiary } from './signUpSlice';
import { Logo } from '../../common/logo/Logo';

export function SignUp() {
  const navigate = useNavigate();
  const subsidiary = useSelector(selectSubsidiary);

  useEffect(() => {
    if (!subsidiary) {
      navigate('/sign-up/subsidiary');
    }
  });

  return (
    <VStack>
      <Center h="20vh"><Logo /></Center>
      <VStack spacing={4}>
        <VStack>
          <Heading size="lg">Let's Get Started!</Heading>
          <Heading size="xs">Create an account to access all the features</Heading>
        </VStack>

        <InputGroup>
          <InputLeftElement pointerEvents="none" children={<Icon as={FiUser} />} />
          <Input placeholder="Name" />
        </InputGroup>

        <InputGroup>
          <InputLeftElement pointerEvents="none" children={<Icon as={FiMail} />} />
          <Input placeholder="Email" type="email" />
          <InputRightAddon children="@subsidia.ry" />
        </InputGroup>

        <InputGroup>
          <InputLeftElement pointerEvents="none" children={<Icon as={FiSmartphone} />} />
          <Input placeholder="Phone" type="tel" />
        </InputGroup>

        <InputGroup>
          <InputLeftElement pointerEvents="none" children={<Icon as={FiLock} />} />
          <Input placeholder="Password" type="password" />
        </InputGroup>

        <InputGroup>
          <InputLeftElement pointerEvents="none" children={<Icon as={FiLock} />} />
          <Input placeholder="Confirm Password" type="password" />
        </InputGroup>

        <Button colorScheme="blue" onClick={() => navigate("/user/home") }>CREATE</Button>
      </VStack>
    </VStack>
  );
}
